import React, {useEffect, useState, useRef} from 'react';
import hostPeer from './utils/host.js';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { List, ListItem, Popper, ClickAwayListener, Grow, Button, Paper, MenuList, MenuItem} from '@material-ui/core';
import {Alert} from '@material-ui/lab'
import DnsIcon from '@material-ui/icons/Dns';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import Loading from './components/Loading.js';
import BasicCard from './components/BasicCard.js';
import useStyles from './styles/styles.js'
import SettingsIcon from '@material-ui/icons/Settings';

const copyToClipboard = str => {
    const el = document.createElement('textarea');
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
};

const Host = ({customSpotify})=> {
    //For error connecting to broker
    const [errorBroker, setErrorBroker] = useState(false)

    //MATERIAL UI STYLE 
    const classes = useStyles();

    //PeerJS stuff
    const [connection, setConnection] =useState(false);
    const [userInfo, setUserInfo] =useState({peerId:null, spotify:null, players:[]});
    
    //Connected clients
    const [connectedClients, setConnectedClients] = useState([]);

    //timeout Time
    const [timeoutTime, setTimeoutTime] = useState(1000);
    const tiemoutTimeRef = useRef(timeoutTime)

    //adjustment menu
    const settingMenuRef = useRef(null)
    const [menu, setMenu] = useState(false)

    useEffect(() => {
        const { spotify, user} = customSpotify

        setUserInfo( val => ({...val, spotify:user}))

        const emit = hostPeer(
            id => setConnection(id),
            error => setErrorBroker(error),
            clients => setConnectedClients(clients)
        )

        const sendSong = async () => {
            setTimeout(sendSong, tiemoutTimeRef.current)

            const data = await spotify.getMyCurrentPlaybackState()
            if(!data) return null;
            
            emit({song: data})   
        }

        setTimeout(sendSong, timeoutTime)

    }, [customSpotify]);

    useEffect(()=>{
        tiemoutTimeRef.current = timeoutTime
    }, [timeoutTime])
    
    if(errorBroker && Object.keys(connectedClients) == 0)
        return <Alert severity="error" className={classes.alert}>Server down, please contact the administrator (Trost xd) and try again later.</Alert>

    if(connection && userInfo.spotify)
    return (
    <div className="App">

        <BasicCard classes={classes}>
            <Typography variant="h6" component="h6">
                <DnsIcon size="large"/>
                Host id
            </Typography>
            <Typography>
                {connection} 
                <IconButton onClick={() => copyToClipboard(connection)} >
                    <FileCopyIcon className={classes.white}/>
                </IconButton>
            </Typography>
        </BasicCard>
                
        <IconButton
            ref={settingMenuRef}
            aria-controls={menu ? 'menu-list-grow' : undefined}
            aria-haspopup="true"
            onClick={()=> setMenu(b => !b)}
            className={classes.configButton}
        >
            <SettingsIcon/>
            {timeoutTime/1000}s

        </IconButton>
        

        <Popper open={menu} anchorEl={settingMenuRef.current} role={undefined} transition disablePortal>
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
            >
              <Paper>
                <ClickAwayListener onClickAway={()=> setMenu(false)}>
                  <MenuList autoFocusItem={menu} id="menu-list-grow" >
                    <MenuItem onClick={()=> {setMenu(false); setTimeoutTime(1*1000)}}>1s</MenuItem>
                    <MenuItem onClick={()=> {setMenu(false); setTimeoutTime(2*1000)}}>2s</MenuItem>
                    <MenuItem onClick={()=> {setMenu(false); setTimeoutTime(3*1000)}}>3s</MenuItem>
                    <MenuItem onClick={()=> {setMenu(false); setTimeoutTime(5*1000)}}>5s</MenuItem>
                    <MenuItem onClick={()=> {setMenu(false); setTimeoutTime(10*1000)}}>10s</MenuItem>
                    <MenuItem onClick={()=> {setMenu(false); setTimeoutTime(15*1000)}}>15s</MenuItem>

                  </MenuList>
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>


        <BasicCard classes={classes}>
            <Typography variant="h6" component="h6">
                {connectedClients.length == 0? 'No connected Users' : 'Connected Users:'}
            </Typography>

            <List>
                {connectedClients.map(id =>  (
                    <ListItem key={id} > 
                        <Typography> {id}</Typography>
                    </ListItem>
                ))}
            </List>
        </BasicCard>

    </div>
    );
    else
    return <Loading />;
}

export default Host;
